/* 기본 스타일 */
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    font-family: 'Noto Sans KR', sans-serif;
    line-height: 1.6;
    color: #333;
    background-color: #fdf2f8; /* 연한 분홍색 배경 */
}

img {
    max-width: 100%;
    height: auto;
}

.App {
    max-width: 100%;
    overflow-x: hidden;
    min-height: 100vh;
    position: relative;
}

/* 청첩장 내용 스타일 */
.invitation-content {
    position: relative;
    opacity: 0;
    transition: opacity 1s ease;
    z-index: -1;
}

.invitation-content.visible {
    opacity: 1;
    z-index: 100;
}

/* WeddingInvitation 컴포넌트 스타일 */
.wedding-invitation {
    width: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    z-index: 100;
    background-color: rgba(255, 255, 255, 0.85);
}

/* 헤더 스타일 */
.header {
    width: 100%;
    padding: 2rem 1rem;
    background-color: rgba(251, 207, 232, 0.7); /* 연한 분홍색 */
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.title {
    font-size: 1.8rem;
    margin-top: 0.5rem;
    margin-bottom: 0.5rem;
    font-family: 'Nanum Myeongjo', serif;
}

.couple-names {
    font-size: 1.2rem;
    margin-bottom: 0.5rem;
}

.wedding-date {
    font-size: 1rem;
    margin-bottom: 1rem;
}

/* 메인 이미지 */
.main-image {
    width: 100%;
    margin: 1rem 0;
}

.main-image img {
    width: 100%;
    height: auto;
    display: block;
}

/* 각 섹션 공통 스타일 */
section {
    padding: 2rem 1rem;
    text-align: center;
    background-color: rgba(255, 255, 255, 0.9);
    margin-bottom: 0.5rem;
    width: 100%;
}

section h2, section h3 {
    font-size: 1.5rem;
    margin-bottom: 1.5rem;
    font-family: 'Nanum Myeongjo', serif;
}

/* 푸터 스타일 */
.footer {
    width: 100%;
    padding: 2rem 1rem;
    background-color: rgba(251, 207, 232, 0.7);
    text-align: center;
    font-size: 0.9rem;
}

/* 모바일 대응 */
@media (max-width: 768px) {
    .title {
        font-size: 1.6rem;
    }

    .couple-names {
        font-size: 1.1rem;
    }

    section {
        padding: 1.5rem 1rem;
    }
}